/* Import background and layout styles */
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Pacifico', cursive;
  background: url('https://images.unsplash.com/photo-1507525428034-b723cf961d3e?auto=format&fit=crop&w=1950&q=80') no-repeat center center fixed;
  background-size: cover;
  overflow-x: hidden;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: white; /* Title stays white */
  padding: 2rem;
  font-family: 'Pacifico', cursive;
}

h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.5);
  color: white; /* Explicitly white */
}

p {
  font-size: 2rem;
  max-width: 600px;
  text-shadow: 1px 1px 4px rgba(0,0,0,0.6);
  animation: fadeIn 2s ease-in;
  color: rgb(5, 5, 5); /* Only paragraph text is orange */
}

/* NFT Image styling */
.nft-image {
  margin: 2rem 0;
  max-width: 90%;
  height: auto;
  border: 4px solid white;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.4);
  animation: zoomIn 1.5s ease-in-out;
}
.neon-orange {
  color: #FFA500;
  text-shadow: 0 0 5px #FFA500, 0 0 10px #FF8C00, 0 0 20px #FF8C00;
  font-weight: bold;
  animation: pulse 2s infinite ease-in-out;
}

@keyframes pulse {
  0% {
    text-shadow: 0 0 5px #FFA500, 0 0 10px #FF8C00, 0 0 20px #FF8C00;
  }
  50% {
    text-shadow: 0 0 10px #FFA500, 0 0 20px #FF8C00, 0 0 30px #FF8C00;
  }
  100% {
    text-shadow: 0 0 5px #FFA500, 0 0 10px #FF8C00, 0 0 20px #FF8C00;
  }
}
/* Button style */
button {
  padding: 0.8rem 1.5rem;
  font-size: 2%;
  background-color: rgba(255, 255, 255, 0.9);
  border: none;
  border-radius: 25px;
  cursor: pointer;
  color: #0077b6;
  transition: transform 0.2s, background-color 0.3s;
  font-weight: bold;
  animation: fadeInUp 1.8s ease-out;
}

button:hover {
  background-color: #ffffff;
  transform: scale(1.05);
}

/* Animations */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeInDown {
  from { opacity: 0; transform: translateY(-30px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes fadeInUp {
  from { opacity: 0; transform: translateY(30px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes zoomIn {
  from { transform: scale(0.8); opacity: 0; }
  to { transform: scale(1); opacity: 1; }
}

/* Responsive */
@media screen and (max-width: 768px) {
  h1 {
    font-size: 2.2rem;
  }

  p {
    font-size: 1.1rem;
  }

  button {
    font-size: 1rem;
  }
}
